import {Component, OnInit, ViewChild, Output, Input, EventEmitter, OnChanges} from '@angular/core';

/**
 * 
 */
@Component({
  selector: 'app-multi-select',
  templateUrl: './multi-select.component.html',
  styleUrls: ['./multi-select.component.scss']
})
export class MultiSelectComponent implements OnInit, OnChanges {
  @ViewChild('op') overlayPanel;
  // 
  @Input() selected; //  , 
  // 
  @Input() title = '';
  // 
  @Output() selectedChange = new EventEmitter<any>();
  // 
  @Input() data = [];
  // 
  list;
  // 
  selectedValue = true;
  search;

  constructor() { }

  ngOnInit() {}

  setSelected() {
    const st = this.selected ? this.selected.split('|') : [];
    let count = 0;
    if (this.data) {
      const oriList = this.data;
      for (let i = 0; i < oriList.length; i++) {
        oriList[i]['selected'] = false;
        for (let j = 0; j < st.length; j++) {
          if (st[j] === oriList[i]['ename']) {
            oriList[i]['selected'] = true;
            count = count + 1;
            break;
          }
        }
      }
      if (count === oriList.length) {
        this.selectedValue = true;
      } else {
        this.selectedValue = false;
      }
      this.list = this.deepClone(this.data);
    }
  }

  ngOnChanges(changes) {
    if (changes.selected && changes.selected.currentValue) {
      this.setSelected();
    }
  }

  /**
   * 
   * @param val
   */
  allSelectedChange(val) {
    this.list.map((item) => {
      if (item['_isFilter']) {
        item['selected'] = val;
      } else {
        item['selected'] = false;
      }
      return item;
    });
    this.selectedValue = val;
  }

  /**
   * 
   */
  toggleState(item) {
    item['selected'] = !item['selected'];
  }

  /**
   * 
   */
  onBeforeHide() {
    this.search = '';
    this.setSelected();
  }

  /**
   * 
   * @param val
   */
  searchChange(val) {
    this.search = val;
    this.allSelectedChange(false);
  }

  /**
   * 
   */
  ensure() {
    const result = this.list.reduce((rs, item) => {
       if (item.selected) {
         if (rs === '') {
           rs += item.ename;
         } else {
           rs += '|' + item.ename;
         }
       }
       return rs;
    }, '');
    this.selectedChange.emit(result);
    this.overlayPanel.hide();
  }

  /**
   * 
   */
  cancel() {
    this.overlayPanel.hide();
  }

  /**
   * 
   * @param source
   * @returns {any}
   */
  deepClone(source) {
    return JSON.parse(JSON.stringify(source));
  }
}



