import { Component, OnInit } from '@angular/core';
import {ResetPasswordService} from '../core/services/reset-password.service';
import {ConfirmationService} from '../primeng/components/common/api';
import {Router} from '@angular/router';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {

  errorMsg =  '';
  account =  '';
  password =  '';
  confirm_password = '';

  showType = 1;  // 1  2
  token;

  constructor(private resetPasswordService: ResetPasswordService, private router: Router,
              private confirmationService: ConfirmationService) { }

  ngOnInit() {
    const location = window.location;
    if (location.search) {
      this.showType = 2;
      let url = location.href.split('?')[1].split('&');
      this.token = url[1].split('=')[1];
    }else {
      this.showType = 1;
    }
  }

  // 
  resetApply() {
    if (this.account == '') {
      this.errorMsg = '~';
      return false;
    }
    const location = window.location;
    this.resetPasswordService.userResetApply({'account': this.account,
      'reset_url': location.protocol + '//' + location.host + '/resetPassword'}).subscribe(
      (succData) => {
        this.confirmationService.confirm({
          message: ``,
          accept : () => {}
        });
      },
      (error) => {
        this.errorMsg = error.message || '';
    });
  }

  // 
  resetPassword() {
    if (this.password == '') {
      this.errorMsg = '~';
      return false;
    }else if (this.confirm_password == '') {
      this.errorMsg = '~';
      return false;
    }else if (this.password != this.confirm_password) {
      this.errorMsg = '';
      return false;
    }else if (this.password.length < 6 || this.password.length > 16) {
      this.errorMsg = '6-16~';
      return false;
    }

    const param = {
      'password': this.password,
      'confirm_password': this.confirm_password,
      'reset_token': this.token
    };
    this.resetPasswordService.userResetPassword(param).subscribe((succData) => {
        this.confirmationService.confirm({
          message: ``,
          accept : () => {
            this.goToLogin();
          }
        });
      },
      (error) => {
        this.errorMsg = error.message || '';
      }
    );
  }

  inputStart() {
    this.errorMsg = '';
  }

  // 
  goToLogin() {
    this.router.navigateByUrl('/login');
  }

}



